var n = 'A10;S20;W10;D30;X;A1A;B10A11;;A10;'
const readarr = n.split(';')
var x = 0
var y = 0
for (let i = 0; i < readarr.length; i++) {
  let head = readarr[i][0]
  if (head == 'A' || head == 'S' || head == 'D' || head == 'W') {
    if (readarr[i].length <= 3 && !isNaN(readarr[i].slice(1))) {
      let tail = parseInt(readarr[i].slice(1))
      if (head == 'A') {
        x = x - tail
      }
      if (head == 'S') {
        y = y - tail
      }
      if (head == 'D') {
        x = x + tail
      }
      if (head == 'W') {
        y = y + tail
      }
    }
  }
}
